html {
    height: 100%;
    box-sizing: border-box;
    font-size: 100%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial,
    sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}
section {
    display: grid;
    grid-template-columns: 1fr minmax(360px, 991px) 1fr;
    grid-template-areas: '. h .' '. c .' '. f .';
    grid-gap: 5px;
    margin-top: 20px;
}

header {
    grid-area: h;
    text-align: center;
}

main {
    grid-area: c;
}

main.content-center {
    display: grid;
    justify-items: center;
}

footer {
    grid-area: f;
    display: grid;
    justify-items: end;
    border-top: 1px solid rgba(34, 36, 38, 0.15);
    margin-top: 20px;
}
.btn__social--list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.btn__social {
    width: 150px;
}

.btn--github {
    background-color: #1b1c1d;
}

.btn--github:hover {
    background-color: #232425;
}

.btn--twitter {
    background-color: #55acee;
}

.btn--twitter:hover {
    background-color: rgb(67, 158, 228);
}

.btn--facebook {
    background-color: #3b5998;
}

.btn--facebook:hover {
    background-color: rgb(45, 77, 146);
}

.btn--google {
    background-color: #3b5998;
}

.btn--google:hover {
    background-color: rgb(45, 77, 146);
}
.btn__profiles--list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.container__profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px;
}

.container__profile--photo {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.container__profile--btn {
    font-size: 0.65rem;
    width: 80px;
    height: 28px;
    background-color: transparent;
    border: 1px solid red;
    color: red;
}

.container__profile--btn:hover {
    background-color: red;
    color: white;
}

